.main{
  &__header{
    position: relative;
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
    gap: 5px 50px;
    max-width: 100%;
    min-height: 184px;
    height: max-content;
    padding: 10px;
    background: $grad-main;
    border-radius: $radius;
    &::before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: url('../img/header-bg.png') no-repeat;
      background-position: calc(100% - 10px) calc(100% - 10px);
      z-index: 1;
    }
  }

}

.mh-badges{
  display: inline-flex;
  flex-direction: column;
  padding: 15px 10px;
  z-index: 2;
  &__top{
    margin-bottom: 3px;
    border-bottom: 1px solid $white;
  }
  &__bot{
    padding-top: 15px;
  }
  &__list{
    display: flex;
    gap: 15px;
  }
  &__item{
    &--clear{
      visibility: hidden;
    }
  }
  &__achive{
    position: relative;
    display: flex;
    @include transit;
    img{
      @include transit;
    }
    @media screen and (min-width: 992px) {
      &:not(&--disabled){
        &:hover{
          img{
            transform: scale(1.05);
          }
        }
      }
    }
    &--counter{
      &::before{
        content: attr(data-count);
        position: absolute;
        top: -6px;
        right: -6px;
        padding: 2px 4px 1px 4px;
        font-size: 12px;
        text-align: center;
        color: $primary;
        background-color: $white;
        border-radius: $radius;
        z-index: 10;
      }
    }
  }
}

.mh-games{
  display: inline-flex;
  flex-direction: column;
  gap: 20px;
  padding: 10px 0;
  z-index: 2;
  &__switcher{
    display: flex;
    justify-content: space-between;
    gap: 10px;
    padding: 5px;
    border: 1px solid $primary;
    border-radius: $radius-small;
  }
  &__btn{
    display: flex;
    justify-content: center;
    width: 210px;
    padding: 7px 0;
    font-weight: 600;
    color: $primary-light;
    background-color: transparent;
    border-radius: 3px;
    @include transit;
    @include over(1);
    @media screen and (min-width: 992px) {
      &:hover{
        color: $black;
        background-color: $primary-light;
      }
    }
    &:active{
      color: $white;
      background-color: $primary;
    }
    &--active{
      color: $white;
      background-color: $primary;
    }
  }
  &__btn-text{
    display: flex;
    justify-content: center;
    padding: 0 10px;
    text-align: center;
    @include over(2);
  }
  &__cards{
    display: flex;
    // justify-content: space-between;
    gap: 20px;
  }
  &__card{
    display: inline-flex;
    flex-direction: column;
    justify-content: space-between;
    width: 95px;
    height: 75px;
    padding: 10px;
    background: $grad-main;
    @include border-gradient;
  }
  &__title{
    max-width: 100%;
    font-weight: 600;
    color: $white;
    @include over(1);
  }
  &__total{
    display: flex;
    justify-content: flex-end;
    max-width: 100%;
    max-height: 100%;
    font-size: 30px;
    color: $secondary;
    overflow: hidden;
    word-wrap: break-word;
    word-break: break-word;
  }
}

.mh-social{
  position: relative;
  display: inline-flex;
  flex-direction: column;
  align-items: flex-start;
  margin-left: -6px;
  z-index: 2;
  &__title{
    max-width: 120px;
    margin-bottom: 8px;
    font-size: 12px;
    color: $primary-light;
    @include over(1);
  }
  &__list{
    display: flex;
    gap: 0 15px;
  }
}

@media screen and (max-width: 1242px) {
  .main{
    &__header{
      justify-content: center;
    }
  }
}

@media screen and (max-width: 625px) {
  .main{
    &__header{
      justify-content: flex-start;
    }
  }
}

@media screen and (max-width: 576px) {
  .main{
    &__header{
      position: relative;
      // left: -20px;
      // right: 0;
      // width: 100vw;
      border-radius: 0;
      &::before{
        content: none;
      }
    }
  }
  .mh-games{
    width: 100%;
    &__cards{
      flex-wrap: wrap;
      justify-content: center;
    }
    &__card{
      min-width: 67px;
      width: auto;
      height: 60px;
    }
    &__title{
      font-size: 12px;
    }
    &__total{
      font-size: 20px;
    }
    &__btn{
      width: 50%;
    }
  }
  .mh-badges{
    width: 100%;
    padding: 15px 0;
    &__list{
      flex-wrap: wrap;
    }
  }
}

