.table-header{
  padding: 20px;
}

.table{
  width: 100%;
  border-spacing: 0;
  box-shadow: $shadow;
  border-bottom-left-radius: $radius;
  border-bottom-right-radius: $radius;
  overflow: hidden;
  &__top{
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: 5fr minmax(40px, 2fr) minmax(30px, 1fr) minmax(30px, 1fr) minmax(40px, 1.5fr) minmax(80px, 1fr);
    text-align: left;
    padding: 0 20px;
    font-size: 12px;
    color: $grey-dark;
    background-color: $white;
    border-bottom: 1px solid $primary-light;
    // @include over(1);
    th{
      position: relative;
      max-width: 90%;
      padding: 20px 0 0;
      line-height: 25px;
      cursor: pointer;
      @include transit;
      word-wrap: break-word;
      word-break: break-word;
      @include over(1);
      &:not(:first-child){
        // margin-left: -15px;
        padding-left: 15px;
        &::before{
          content: '';
          position: absolute;
          left: 0;
          bottom: 7px;
          width: 6px;
          height: 11px;
          background-image: url('../img/table-arrow.svg');
          background-repeat: no-repeat;
        }
        @media screen and (min-width: 992px) {
          &:hover{
            &::before{
              transform: rotate(180deg);
              @include transit(.2);
            }
          }
        }
      }
      @media screen and (min-width: 992px) {
        &:hover{
          color: $primary-light;
        }
      }
    }
  }
  &__row{
    display: grid;
    grid-auto-flow: column;
    // grid-template-columns: 5fr 2fr 1fr 1fr 1.5fr 1fr;
    grid-template-columns: 5fr minmax(40px, 2fr) minmax(30px, 1fr) minmax(30px, 1fr) minmax(40px, 1.5fr) minmax(80px, 1fr);
    align-items: center;
    padding: 20px 10px;
    word-wrap: break-word;
    word-break: break-word;
    &:nth-child(2n){
      background-color: $white;
    }
    td{
      padding: 0 10px;
    }
  }
  &__rating{
    font-weight: 600;
    color: $primary;
  }
}

@media screen and (max-width: 576px) {
  .table-wrapper{
    overflow-x: scroll;
    &::-webkit-scrollbar {
      -webkit-appearance: none;
      width: 5px;
    }
  }
  .table{
    width: 150%;
  }
}