.sidebar{
  min-height: 800px;
  box-shadow: $shadow;
  border-bottom-left-radius: $radius;
  border-bottom-right-radius: $radius;
  &__header{
    display: flex;
    flex-direction: column;
    min-width: 295px;
    min-height: 184px;
    padding: $p-big;
    background: $grad-main;
    border-radius: $radius;
  }
  &__header-top{
    display: flex;
    align-items: flex-start;
  }
  &__header-bot{
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-top: auto;
  }
  &__avatar{
    position: relative;
    margin-right: 10px;
    &.avatar{
      max-width: 70px;
      max-height: 70px;
      border: 3px solid transparent;
      &::before{
        content: none;
        border: 2px solid $secondary;
      }
    }
  }
  &__rate{
    display: flex;
    align-items: flex-start;
    margin-bottom: 5px;
  }
  &__rate-val{
    margin-right: 2px;
    font-size: 30px;
    line-height: 33px;
    color: $white;
    @include over(1);
  }
  &__rate-numbers{
    display: flex;
    align-items: self-end;
    padding: 2px 7px;
    font-size: 12px;
    color: $white;
    background-color: $primary;
    border-radius: $radius;
    span{
      @include over(1);
    }
  }
  &__name{
    display: flex;
    flex-direction: column;
    max-width: 100%;
    font-size: 20px;
    line-height: 22px;
    color: $white;
    word-wrap: break-word;
    word-break: break-word;
    span{
      @include over;
    }
  }
  &__change{
    display: inline-flex;
    align-items: center;
    .pen{
      margin-right: 5px;
    }
    @media screen and (min-width: 992px) {
      &:hover{
        .sidebar__change-text{
          color: lighten($primary, 10);
          text-decoration: none;
        }
        .pen{
          stroke: lighten($primary, 10);
        }
      }
    }
  }
  &__change-text{
    max-width: 90%;
    font-size: 12px;
    line-height: 13px;
    color: $primary-light;
    text-decoration: underline;
    text-underline-offset: 1px;
    @include transit;
    @include over(1);
  }

  &__menu{
    padding: 50px 20px;
    border-bottom-left-radius: $radius;
    border-bottom-right-radius: $radius;
  }
  &__submenu{
    display: flex;
    flex-direction: column;
    gap: 5px;
    padding: 5px 0 0 20px;
    // transform: scaleY(0);
    // transform-origin: center top;
    // @include transit;
    // &--active{
    //   transform: scaleY(1);
    // }
  }
  &__sublink{
    padding: 4px 10px;
  }
  &__list{
    display: flex;
    flex-direction: column;
    gap: 5px;
    width: 85%;
    max-width: 100%;
  }
  &__item{
    &:last-child{
      margin-top: 60px;
    }
  }
  &__link{
    display: flex;
    align-items: center;
    gap: 10px;
    min-height: 32px;
    padding: 4px 15px;
    border-radius: $radius-small;
    @include transit;
    &--active{
      background-color: $primary-low;
      .sidebar__link-text{
        color: $primary;
      }
      @media screen and (min-width: 992px) {
        &:hover{
          .arrow-icon{
            stroke: $primary !important;
          }
        }
      }
    }
    @media screen and (min-width: 992px) {
      &:hover{
        background-color: $primary-low;
        .sidebar__link-text{
          color: $primary-light;
        }
        .arrow-icon{
          stroke: $primary-light;
        }
      }
    }
  }
  &__link-text{
    line-height: 22px;
    @include transit;
  }
  &__link-icon{
    display: flex;
    &--arrow{
      margin-left: auto;
      margin-right: 0;
      @include transit;
      transform: rotate(180deg);
      &.active{
        transform: rotate(0);
      }
    }
  }
  
}


.arrow-small,
.pen{
  width: 14px;
  height: 14px;
}

.pen{
  stroke: $primary-light;
  stroke-width: 1.5;
  @include transit;
}

.sidebar-icon{
  width: 24px;
  height: 24px;
}
.sidebar-icon-outline{
  width: 24px;
  height: 24px;
  stroke: $primary-light;
  stroke-width: 1.5;
}


@media screen and (max-width: 1024px) {
  .sidebar{
    &__list{
      width: 100%;
    }
  }
}

@media screen and (max-width: 902px) {
  .sidebar{
    &__header{
      min-width: 250px;
      max-width: 250px;
      padding: 10px;
    }
  }
}

@media screen and (max-width: 768px) {
  .sidebar{
    position: relative;
    display: flex;
    justify-content: space-between;
    min-height: unset;
    background: $grad-main;
    border-radius: $radius;
    &__header{
      background: none;
    }
    &__menu{
      position: absolute;
      top: 184px;
      padding: 10px 0;
      z-index: 500;
    }
    &__list{
      flex-direction: row;
      align-items: flex-start;
      overflow-x: scroll;
      gap: 20px;
      padding: 10px 0;
      flex: 1 1 auto;
      &::-webkit-scrollbar {
        -webkit-appearance: none;
        width: 5px;
      }
    }
    &__item{
      max-width: 200px;
      display: flex;
      flex-direction: column;
      flex: 1 0 auto;
      &:last-child{
        margin-top: 0;
      }
    }
    &__link{
      min-height: 40px;
    }
    &__submenu{
      margin-top: 5px;
      padding: 10px;
      background-color: $white;
      border-radius: $radius-small;
      // overflow: hidden;
    }
    &__sublink{
      padding: 0 5px;
      color: $grey-dark;
    }
    &__header-bot{
      .sidebar__item{
        margin-top: 0;
      }
      .sidebar__link{
        justify-content: flex-end;
        gap: 5px;
        min-height: unset;
        padding: 0;
        color: $primary-light;
        .sidebar-icon{
          width: 17px;
          height: 17px;
        }
      }
    }
    .mh-badges{
      justify-content: center;
      padding-right: 40px;
    }
  }
}

@media screen and (max-width: 620px) {
  .sidebar{
    &__header{
      min-width: auto;
      width: 100%;
      max-width: 100%;
    }
    &__header-bot{
      .sidebar__item{
        display: none;
      }
      .mh-social{
        display: inline-flex !important;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .sidebar{
    border-radius: 0;
    &__menu{
      padding: 10px 20px;
    }
  }
}

@media screen and (max-width: 320px) {
  .sidebar{
    border-radius: 0;
    &__menu{
      padding: 10px;
    }
  }
}
