.footer{
  padding: 50px 0 20px 0;
  background-color: $black;
  &__top{
    display: flex;
    justify-content: space-between;
    gap: 50px;
    border-bottom: 1px solid $white;
  }
  &__bot{
    display: flex;
    justify-content: space-between;
    gap: 50px;
    padding: 20px 0;
  }
  &__top-left{
    display: inline-flex;
    flex-direction: column;
    margin-bottom: 30px;
  }
  &__top-right{
    display: flex;
    flex: 1 1 auto;
  }
  &__logo{
    display: inline-flex;
    align-items: center;
    margin-bottom: 30px;
    .logo__icon,
    .logo__text{
      fill: $white;
    }
    @media screen and (min-width: 992px){
      &:hover{
        .logo__icon,
        .logo__text{
          fill: $primary-light;
        }
      }
    }
  }
  &__block-list{
    display: flex;
    justify-content: space-around;
    flex: 1 1 100%;
    gap: 10px;
  }
  &__list{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__item{
    &:not(&--head):not(:last-child){
      margin-bottom: 10px;
    }
    &--head{
      margin-bottom: 20px;
      max-width: 180px;
      word-wrap: break-word;
      word-break: break-word;
      color: $primary-light;
    }
  }
  &__link{
    display: inline-flex;
    align-items: center;
    max-width: 180px;
    color: $white;
    word-wrap: break-word;
    word-break: break-word;
    @include transit;
    @media screen and (min-width: 992px) {
      &:hover{
        color: $primary-light;
        .footer__link-icon{
          stroke: $primary-light;
        }
      }
    }
  }
  &__link-icon{
    width: 24px;
    height: 24px;
    margin-right: 10px;
    stroke: $white;
    stroke-width: 1.5;
    @include transit;
  }
  &__copy,
  &__made{
    max-width: 100%;
    color: $white;
    word-wrap: break-word;
    word-break: break-word;
  }
  &__social-block{
    display: flex;
    gap: 15px;
  }
}




.dwl{
  display: flex;
  gap: 10px;
  margin-bottom: 50px;
  &__link{
    display: inline-flex;
    align-items: center;
    border-radius: $radius-small;
    background: $grad-main;
    @include transit;
    @include border-gradient;
    @media screen and (min-width: 992px) {
      &:hover{
        transform: scale(1.05);
      }
    }
  }
  &__appstore{
    padding: 5px 10px 5px 5px;
  }
  &__playmarket{
    padding: 5px 9px;
  }
}

.dwl-icon{
  fill: $white;
}

.dwl-apple{
  width: 32px;
  height: 32px;
  margin-right: 5px;
}

.dwl-play{
  width: 24px;
  height: 32px;
  margin-right: 10px;
}

.dwl-text{
  width: 84px;
  height: 30px;
}


@media screen and (max-width: 902px) {
  .footer{
    &__top-right{
      display: none;
    }
    &__top-left{
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      flex: 1 1 100%;
    }
    &__block-list{
      justify-content: flex-start;
      flex-wrap: wrap;
      gap: 20px;
      .footer__link{
        color: $black;
      }
    }
    .dwl{
      margin-bottom: 0;
    }
  }
}

@media screen and (max-width: 690px) {
  .footer{
    &__top-left{
      flex-direction: column-reverse;
      gap: 40px;
      .footer__item--head{
        display: none;
      }
      .footer__list{
        flex-direction: row;
        justify-content: center;
        flex-wrap: wrap;
        gap: 0 40px ;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .footer{
    &__top{
      border-bottom: none;
    }
    &__top-left{
      margin-bottom: 0;
    }
    &__bot{
      flex-direction: column;
      justify-content: center;
      align-items: center;
      gap: 5px;
    }
    &__block-list{
      justify-content: space-between;
    }
    &__block-item{
      width: 45%;
    }
  }
}