.header{
  min-height: 80px;
  padding: 20px 0;
  background-color: $grey-light;
  box-shadow: $shadow-small;
  &__container{
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
  }
  &__logo{
    display: flex;
    align-items: center;
    max-height: 32px;
    @media screen and (min-width: 992px) {
      &:hover{
        .logo{
          &__icon{
            animation: rotate-scale-up 0.45s linear both;
          }
          &__text{
            fill: $primary;
          }
        }
      }
    }
  }
  // &__nav{
  // }
  &__right{
    display: flex; 
    align-items: center;
    justify-content: space-between;
    gap: 0 10px;
    flex: 0 1 20%;
  }
  &__selector{
    margin-right: 30px;
  }
  &__badge{
    position: relative;
    &::before{
      content: attr(data-counter);
      position: absolute;
      right: -6px;
      bottom: -2px;
      display: flex;
      align-items: center;
      justify-content: center;
      min-width: 18px;
      height: 18px;
      padding-top: 1px;
      color: $white;
      font-size: 12px;
      text-align: center;
      background-color: $cyan;
      border: 2px solid $grey-light;
      border-radius: 50%;
    }
    @media screen and (min-width: 992px) {
      &:hover{
        .bag{
          fill: $primary;
        }
      }
    }
    &:active{
      .bag{
        fill: $primary-dark;
      }
    }
  }
}

.logo{
  &__icon{
    width: 32px;
    height: 32px;
    margin-right: 20px;
    fill: $black;
    @include transit;
  }
  &__text{
    max-width: 135px;
    max-height: 12px;
    fill: $black;
    @include transit;
  }
}

.menu{
  display: flex;
  &__list{
    display: flex;
    gap: 0 50px;
  }
  &__link{
    display: flex;
    align-items: center;
    padding: 12px 0;
    &--more{
      position: relative;
      display: flex;
      gap: 0 12px;
      .menu__arrow{
        z-index: -1;
      }
      @media screen and (min-width: 992px){
        &:hover{
          .arrow-icon{
            stroke: $primary;
          }
        }
      }
    }
  }
  &__arrow{
    display: flex;
  }
  .burger{
    display: none;
  }
}

.arrow-icon{
  width: 12px;
  height: 12px;
  stroke: $primary-light;
  stroke-width: 1.5;
  @include transit;
}

.selector{
  display: flex;
  align-items: center;
  gap: 0 12px;
  padding: 8px 10px;
  background-color: $white;
  border-radius: $radius-small;
  @include transit;
  &__icon{
    width: 24px;
    height: 24px;
  }
  @media screen and (min-width: 992px){
    &:hover{
      box-shadow: $shadow-small;
      .arrow-icon{
        stroke: $primary;
      }
    }
  }
}

.avatar{
  position: relative;
  display: flex;
  flex: 1 0 auto;
  max-width: 36px;
  max-height: 36px;
  border: 1px solid $grey-light;
  border-radius: 50%;
  z-index: 10;
  &::before{
    content: '';
    position: absolute;
    left: -1px;
    top: -1px;
    right: -1px;
    bottom: -1px;
    margin: -4px;
    border: 3px solid $primary;
    border-radius: inherit;
    z-index: -1;
    @include transit;
  }
  picture,
  img{
    display: block;
    max-width: 100%;
    max-height: 100%;
    border-radius: 50%;
    overflow: hidden;
  }
  @media screen and (min-width: 992px) {
    &:hover{
      &::before{
        border-color: $secondary;
      }
    }
  }
}

.bag{
  width: 21px;
  height: 28px;
  fill: $black;
  @include transit;
}


@keyframes rotate-scale-up {
  0% {
    -webkit-transform: scale(1) rotateZ(0);
            transform: scale(1) rotateZ(0);
  }
  50% {
    -webkit-transform: scale(1.5) rotateZ(180deg);
            transform: scale(1.5) rotateZ(180deg);
                 fill: $primary;
  }
  100% {
    -webkit-transform: scale(1) rotateZ(360deg);
            transform: scale(1) rotateZ(360deg);
  }
}

//* burger ->
:root {
  --font-size: 1;
  --color1: #5A60E7;
  --borderRadius: 0.5px;
}

.burger {
  display: inline-block;
  font-size: var(--font-size);
  cursor: pointer;
  padding: 20px 10px;
  & > div {
    width: 24px;
    height: 3px;
    border-radius: var(--borderRadius);
    background: var(--color1);
    &::before,
    &::after {
      transition: font-size 0s;
      content: "";
      display: block;
      position: absolute;
      width: 24px;
      height: 3px;
      background: var(--color1);
      border-radius: var(--borderRadius);
    }
    &::before {
      margin-top: -10px;
      width: 13px;
    }
    &::after {
      margin-top: 10px;
    }
  }
}

.burger {
  & > div {
    transition: background 0.4s, font-size 0s;
    &::before,
    &::after {
      transition: font-size 0s;
      transform-origin: center center;
    }
  }
  &.toggled {
    > div::before {
      width: 24px;
      animation: burg2top 0.4s linear forwards;
    }
    > div {
      background: transparent;
    }
    > div::after {
      animation: burg2bottom 0.4s linear forwards;
    }
  }
  &.unToggled {
    > div::before {
      animation: burg2topReset 0.4s linear forwards;
    }
    > div {
      background: var(--color1);
    }
    > div::after {
      animation: burg2bottomReset 0.4s linear forwards;
    }
  }

  @keyframes burg2top {
    0% {
    }
    20% {
      margin-top: 0em;
      transform: rotate(0deg);
    }
    60% {
      margin-top: 0em;
      transform: rotate(55deg);
    }
    100% {
      margin-top: 0em;
      transform: rotate(45deg);
    }
  }

  @keyframes burg2bottom {
    0% {
    }
    20% {
      margin-top: 0em;
      transform: rotate(0deg);
    }
    60% {
      margin-top: 0em;
      transform: rotate(-55deg);
    }
    100% {
      margin-top: 0em;
      transform: rotate(-45deg);
    }
  }

  @keyframes burg2topReset {
    0% {
      margin-top: 0em;
      transform: rotate(45deg);
    }
    20% {
      transform: rotate(0deg);
    }
    60% {
      margin-top: 12px;
      transform: rotate(0deg);
    }
    100% {
      margin-top: 10px;
      transform: rotate(0deg);
    }
  }

  @keyframes burg2bottomReset {
    0% {
      margin-top: 0em;
      transform: rotate(-45deg);
    }
    20% {
      transform: rotate(0deg);
    }
    60% {
      margin-top: -12px;
      transform: rotate(0deg);
    }
    100% {
      margin-top: -10px;
      transform: rotate(0deg);
    }
  }
}
//* burger <-


@media screen and (max-width: 1024px) {
  .header{
    position: relative;
    z-index: 1000;
    &__right{
      flex: 0 1 auto;
    }
  }
  .menu{
    // position: relative;
    order: -1;
    &__burger{
      display: inline-block !important;
      z-index: 550;
    }
    &__item{
      border-bottom: 1px solid $primary-low;
    }
    &__list{
      position: absolute;
      top: 80px;
      left: 0;
      flex-direction: column;
      gap: 20px 50px;
      padding: 40px;
      width: 100vw;
      background-color: $white;
      z-index: 500;
      pointer-events: none;
      user-select: none;
      opacity: 0;
      overflow: auto;
      transform: translateY(-200%);
      @include transit;
      &.active{
        pointer-events: all;
        user-select: auto;
        opacity: 1;
        transform: translateY(0);
      }
    }
  }
  .hidden {
    position: fixed;
    width: 100%;
    height: 100vh;
    // padding-right: 15px;
    overflow: hidden;
  }
}

@media screen and (max-width: 902px) {
  .menu{
    &__list{
      padding: 40px 20px;
      overflow-y: auto;
      height: calc(100vh - 80px);
    }
  }
}

@media screen and (max-width: 576px) {
  .header{
    &__right{
      margin-left: auto;
      order: 2;
    }
    &__nav{
      order: 3;
    }
    &__avatar{
      display: none;
    }
    &__selector{
      margin-right: 0;
    }
  }
}

@media screen and (max-width: 320px) {
  .logo{
    &__icon{
      width: 20px;
      height: 20px;
    }
    &__text{
      max-width: 90px;
    }
  }
}