.link{
  max-width: 100%;
  color: $black;
  @include transit;
  word-wrap: break-word;
  word-break: break-word;
  @media screen and (min-width: 992px) {
    &:hover{
      color: $primary;
    }
  }
  &:focus-visible{
    outline-color: $primary-light;
  }
  &:active{
    color: $primary-dark;
  }
  &--primary{
    color: $primary;
    @media screen and (min-width: 992px) {
      &:hover{
        color: $black;
      }
    }
    &:focus-visible{
      outline-color: $primary-light;
    }
    &:active{
      color: darken($black, 20);
    }
  }
}

.text{
  max-width: 100%;
  word-wrap: break-word;
  word-break: break-word;
}