$black: #222;
$white: #fff;

// $grey: #999;
$grey-light: #FAF9F9;
$grey-dark: #909090;
// $grey-md: #eaeaea;

$primary: #5A60E7;
$primary-light: #ACAFF3;
$primary-low: #EFEFFD;
$primary-dark: darken($primary, 20);

$secondary: #FF795B;

$cyan: #86E3CE;


$grad-main: linear-gradient(238.37deg, rgba(90, 96, 231, 0.35) 6.34%, rgba(30, 30, 40, 0) 103.77%), #2C2C2C;

$grad-border: linear-gradient(143.66deg, #6171FF -9.12%, rgba(90, 96, 231, 0) 40.43%),
linear-gradient(120.87deg, rgba(97, 113, 255, 0) 70.49%, #6171FF 90.53%);


$radius: 10px;
$radius-small: 5px;

$shadow: 0px 2px 8px #E9E9E9;
$shadow-small: 0px 2px 4px #E9E9E9;

$p-min: 5px 10px;
$p-big: 20px;



@mixin border-gradient {
  position: relative;
  border-radius: $radius-small;
  &::before{
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    border-radius: inherit;
    background-image: radial-gradient(circle at 100% 100%, transparent 5px, #6171ff 5px, #6171ff 6px, transparent 6px), linear-gradient(to right, #6171ff, #3c3e6d), radial-gradient(circle at 0% 100%, transparent 5px, #3c3e6d 5px, #3c3e6d 6px, transparent 6px), linear-gradient(to bottom, #3c3e6d, #6171ff), radial-gradient(circle at 0% 0%, transparent 5px, #6171ff 5px, #6171ff 6px, transparent 6px), linear-gradient(to left, #6171ff, #2c2c2c), radial-gradient(circle at 100% 0%, transparent 5px, #2c2c2c 5px, #2c2c2c 6px, transparent 6px), linear-gradient(to top, #2c2c2c, #6171ff);
    background-size: 6px 6px, calc(100% - 12px) 1px, 6px 6px, 1px calc(100% - 12px);
    background-position: top left,top center,top right,center right, bottom right,bottom center,bottom left,center left;
    background-repeat: no-repeat;
  }
}

